p {
    font-family: Arial, Helvetica, sans-serif;
}

.page-header {
    background-color: #f2f2f2;
    padding-top: 50px;
    padding-bottom:55px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;

    border-bottom: 6px solid #ffc000;
}

.header-left {
    flex-grow: 4.5;
}

.header-right {
    flex-grow: 1;
}

#name, #job {
    font-size: 24px;
    margin: 6px;
    line-height: 0.8;
}

.contacts {
    font-size: 12px;
    margin-top: 12px;
}